import classnames from 'classnames';
import { forwardRef } from 'react';

import { CHECKBOX_SIZE_SMALL, CLASS_IS_DISABLED } from './Checkbox.constants';
import { Wrapper, HiddenInput, Label, Tick } from './Checkbox.styles';
import { Props } from './Checkbox.types';

const Checkbox = forwardRef<HTMLInputElement, Props>(
  function Checkbox(props, ref) {
    const {
      checked,
      children,
      disabled = false,
      id,
      onChange,
      size = CHECKBOX_SIZE_SMALL,
    } = props;
    return (
      <Wrapper
        $size={size}
        className={classnames({
          [CLASS_IS_DISABLED]: disabled,
        })}
        ref={ref}
      >
        <HiddenInput
          checked={checked}
          disabled={disabled}
          id={id}
          onChange={onChange}
          type="checkbox"
        />
        <Label htmlFor={id} $size={size} $disabled={disabled}>
          <Tick />
          {children}
        </Label>
      </Wrapper>
    );
  },
);

export default Checkbox;
