import { styled } from 'styled-components';

import { visuallyHidden } from '#theme/mixins';

import TickIcon from '#assets/icons/check-solid-16.svg';

import { HiddenInputProps, LabelProps, WrapperProps } from './Checkbox.types';

export const HiddenInput = styled.input<HiddenInputProps>`
  ${visuallyHidden}
`;

export const Label = styled.label<LabelProps>`
  display: flex;
  align-items: center;
  gap: var(--gap);
  color: var(--color);
  cursor: ${({ $disabled }) => ($disabled ? 'default' : 'pointer')};
  font: ${({ theme, $size }) => theme.textStyle.checkbox[$size]};
  text-align: center;
  user-select: none;
`;

export const Tick = styled(TickIcon)`
  width: var(--width);
  height: var(--height);
  border: 1px solid ${({ theme }) => theme.color.divider.black};

  path {
    display: none;
  }
`;

export const Wrapper = styled.div<WrapperProps>`
  --height: ${({ $size }) => {
    switch ($size) {
      case 'small':
        return '16px';
      case 'large':
        return '32px';
      default:
        return '24px';
    }
  }};
  --width: var(--height);
  --gap: calc(var(--width) / 2);
  --color: ${({ theme }) => theme.color.ink.base};
  position: relative;
  background-color: var(--primary-color);
  color: var(--color);

  &:focus,
  &:hover,
  &:active {
    background-color: var(--hover-color);
    color: var(--color);
  }

  &.is-disabled,
  &:disabled {
    --color: ${({ theme }) => theme.color.semiotic.disabled};

    ${Tick} {
      border-color: ${({ theme }) => theme.color.divider.light};
      background: ${({ theme }) => theme.color.canvas.secondary};
    }
  }

  :checked + ${Label} path {
    display: initial;
  }
`;
